body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height: 100%;
  width: 100%;
}

.root {
  height: 100%;
  width: 100%;
}

.container {
  background-color: #FFCC99;
  height: 100%;
  width: 100%;
  display: flex;
}

.container__pic {
  width: 40%;
  object-fit: cover;
}

.container__infos {
  width: 60%;
  padding: 130px 30px ;
  display: flex;
  flex-direction: column;

}

.infos__name {
  color: #933EC5;
  font-family: Arial Black; 
  text-align: center;
}

.infos__bio {
  color: #5F4F77;
}

.bio__list-social {
  display: flex;
  list-style: none;
  justify-content: center;
  padding: 0;
}

.list-social__social {
}

.social__icon {
  margin-top: 10px;
  margin-right: 20px;
}
